.Join{
    display: flex;
    padding: 0 5rem;
    gap: 10rem;
    height: 5rem;
}
.left-j{
    color: white;
    font-size: 3rem;
    font-weight: bold;
    text-transform: uppercase;
    position: relative;
}

.left-j>hr{
    position: absolute;
    width: 10rem;
    border: 2px solid var(--green);
    border-radius: 20%;
    margin: -10px 0; 
}

.left-j>div{
    display: flex;
    gap: 2rem;
}

.right-j{
    display: flex;
    justify-content: center;
    align-items: flex-end;
}
.email-container{
    display: flex;
    gap: 3rem;
    background-color: gray;
    padding: 1rem 2rem;
}

.email-container>input{
    background-color: transparent;
    border: none;
    outline: none;
    color: white;
    cursor: pointer;
}

::placeholder{
    color: var(--lightgray);
}

.btn-j{
    background-color: var(--green);
    color: white;
}


@media screen and (max-width: 768px){
    .Join{
        flex-direction: column;
        gap: 1rem;
    }
    .left-j{
        font-size: x-large;
        flex-direction: column;
    }
    .right-j{
        padding: 2rem;
    }
}













